.Icon-container-check{position:absolute;left:12px}
.Icon-container-check--active{color:#4caf50}
.Icon-container-delete{position:absolute;top:-24px;right:0}
.Icon-container-delete:hover{color:red}
.Icon-svg {
width: 24px;
height: 24px;
}
.Icon-container-delete:hover .Icon-svg {fill:red}
.Icon-container-check:hover .Icon-svg {fill: green}
